  padding: 0;
  box-sizing: inherit;
}

body {
  background-color: #FCFCFC;
  font-family: "Quattrocento", serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  margin: 0;
  color: #333333;
  font-size: 1.6rem;
  line-height: 1.5;
}

hr {
  margin: 0;
  background-color: #F29524;
  height: 0.3rem;
  border: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a,
p {
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  line-height: 1.2;
  font-family: "Quattrocento", serif;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 5rem;
}

h2 {
  color: #332A1F;
  display: inline-block;
  font-size: 3.4rem;
}

h3 {
  color: #332A1F;
  font-size: 2.5rem;
}

h4 {
  color: #332A1F;
  font-size: 2rem;
}

h5 {
  color: #332A1F;
  font-size: 1.6rem;
}

.marcador-pagina {
  margin-top: 4rem;
}
.marcador-pagina h1 {
  display: inline-block;
  color: #333333;
  font-size: 1.6rem;
  line-height: 1.5;
}

.boton_anar {
  text-align: center;
  display: inline-block;
  padding: 0.8rem 3.2rem;
  background-color: #F29524;
  color: #FCFCFC;
  transition: all 0.2s ease;
}
.boton_anar:hover {
  background-color: #f5ac54;
}

.seccion {
  padding: 4rem 0;
}

.seccion {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

.contenido-centrado {
  max-width: 800px;
}

.no-webp .inicio {
  background-image: url("../../../build/img/header.jpg");
}

.webp .inicio {
  background-image: url("../../../build/img/header.webp");
}

.header {
  background-color: #332A1F;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 65rem;
  }
}
.header.inicio .contenido-header {
  height: 100vh;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 65rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FCFCFC;
  text-align: left;
  margin: 0;
  margin-bottom: 25rem;
  max-width: 75rem;
}
@media (min-width: 1024px) {
  .contenido-header h1 {
    margin-bottom: 12rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding: 1.6rem 0 2.4rem 0;
}
.barra img {
  height: 4rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .boton-menu {
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #332A1F;
  width: 5.6rem;
  height: 5.6rem;
  border-radius: 50%;
  position: fixed;
  bottom: 3.2rem;
  right: 3.2rem;
}
@media (min-width: 768px) {
  .barra .boton-menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .barra .derecha {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.barra .derecha .dark-mode-boton {
  cursor: pointer;
  filter: invert(100%);
  margin-left: 1.6rem;
  height: 2.4rem;
}
@media (max-width: 768px) {
  .barra .derecha .dark-mode-boton {
    position: absolute;
    top: 2.4rem;
    right: 2.4rem;
    z-index: 0;
  }
}
.barra .navegacion a {
  color: #22333B;
  font-size: 1.6rem;
  display: block;
  text-align: center;
  margin-right: 2.4rem;
}
@media (min-width: 768px) {
  .barra .navegacion {
    position: inherit;
    background-color: transparent;
    display: flex;
    align-items: center;
  }
  .barra .navegacion a {
    color: #FCFCFC;
    font-size: 1.6rem;
    display: block;
    text-align: center;
    margin-right: 2.4rem;
  }
  .barra .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (max-width: 768px) {
  .barra .navegacion {
    padding: 0.8rem 0;
    top: 0;
    left: 0;
    position: fixed;
    background-color: #FCFCFC;
    width: 100%;
    box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
    z-index: 100;
  }
  .barra .navegacion a {
    padding: 0.8rem 2.4rem;
    margin: 0;
    text-align: left;
    transition: all 0.3s ease;
  }
  .barra .navegacion a:hover {
    color: #FCFCFC;
    background-color: #332A1F;
  }
}
@media (max-width: 768px) {
  .barra .hide {
    display: none;
  }
}

.sobre-nosotros {
  padding: 4rem 0 8rem 0;
}
@media (min-width: 1024px) {
  .sobre-nosotros .iconos {
    display: flex;
  }
}
.sobre-nosotros .iconos .icono {
  margin: 0 auto;
  margin-top: 6rem;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.sobre-nosotros .iconos .icono h3 {
  display: inline-block;
  margin: auto;
  margin-bottom: 1.6rem;
}
.sobre-nosotros .iconos .icono h3 hr {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .sobre-nosotros .iconos .icono {
    width: 35rem;
  }
}
.sobre-nosotros .iconos .icono img {
  height: 8rem;
  margin-bottom: 3.2rem;
}
.sobre-nosotros .iconos .icono h3 {
  text-align: center;
}
.sobre-nosotros .iconos .icono p {
  text-align: center;
}

.sobre-nosotros-experiencia {
  padding: 4rem 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
}
@media (min-width: 1024px) {
  .sobre-nosotros-experiencia {
    grid-template-columns: 1fr 1fr;
  }
}
.sobre-nosotros-experiencia picture,
.sobre-nosotros-experiencia img {
  margin: auto;
  width: 100%;
  height: auto;
}
.sobre-nosotros-experiencia p {
  margin-top: 2.4rem;
}

.anuncios {
  padding: 4rem 0 8rem 0;
}
.anuncios .ver-mas {
  display: flex;
  justify-content: center;
}
.anuncios .ver-mas .boton-ver-mas {
  text-align: center;
  display: block;
  padding: 0.8rem 3.2rem;
  background-color: #22333B;
  color: #FCFCFC;
  transition: all 0.2s ease;
  margin-top: 2.4rem;
}
.anuncios .ver-mas .boton-ver-mas:hover {
  background-color: #354f5b;
}
@media (min-width: 768px) {
  .anuncios .ver-mas {
    justify-content: flex-end;
  }
  .anuncios .ver-mas .boton-ver-mas {
    display: inline-block;
  }
}
.anuncios .contenedor-anuncios {
  margin-top: 3.2rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .anuncios .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .anuncios .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncios .contenedor-anuncios .anuncio {
  margin: 0 auto;
  max-width: 38rem;
  background-color: #FCFCFC;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
}
.anuncios .contenedor-anuncios .anuncio img,
.anuncios .contenedor-anuncios .anuncio picture {
  width: 100%;
  display: block;
}
.anuncios .contenedor-anuncios .anuncio picture {
  overflow: hidden;
  height: 20rem;
  display: flex;
  align-items: center;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio {
  padding: 1.6rem 2.4rem 3.2rem 2.4rem;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio h3,
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio h4 {
  text-align: left;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio p {
  margin-top: 0.8rem;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio h4 {
  color: #F29524;
  margin-top: 1.2rem;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio .boton {
  margin-top: 1.6rem;
  text-align: center;
  display: block;
  padding: 0.8rem 3.2rem;
  background-color: #F29524;
  color: #FCFCFC;
  transition: all 0.2s ease;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio .boton:hover {
  background-color: #f5ac54;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio .iconos-caracteristicas {
  margin: 0;
  padding: 0;
  list-style: 0;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(3, 1fr);
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio .iconos-caracteristicas li {
  display: flex;
  align-items: center;
  margin-top: 1.6rem;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio .iconos-caracteristicas li p {
  margin-bottom: 0.6rem;
}
.anuncios .contenedor-anuncios .anuncio .contenido-anuncio .iconos-caracteristicas li img {
  height: 2.4rem;
  width: auto;
  margin-right: 1.6rem;
}

.contacto {
  padding-top: 4rem;
}

.no-webp .banner {
  background-image: url("../../../build/img/encuentra.jpg");
}

.webp .banner {
  background-image: url("../../../build/img/encuentra.webp");
}

.banner {
