  margin-top: 5.6rem;
}
.banner a {
  margin-top: 1.6rem;
  text-align: center;
  display: inline-block;
  padding: 0.8rem 3.2rem;
  background-color: #F29524;
  color: #FCFCFC;
  transition: all 0.2s ease;
}
.banner a:hover {
  background-color: #f5ac54;
}

.testimonios {
