@media (min-width: 768px) {
  .footer .contenedor-footer {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.footer .contenedor-footer .leyenda img {
  height: 4rem;
}
.footer .contenedor-footer .navegacion {
  margin: 0 auto;
  display: none;
  flex-direction: column;
  align-items: left;
  justify-content: center;
}
.footer .contenedor-footer .navegacion a {
  color: #FCFCFC;
  font-size: 1.6rem;
}
.footer .contenedor-footer .navegacion a:last-of-type {
  margin-right: 0;
}
@media (min-width: 768px) {
  .footer .contenedor-footer .navegacion {
    display: flex;
  }
}
.footer .copy-right {
  text-align: center;
  font-size: 1.4rem;
  color: #FCFCFC;
}

.dark-mode {
  background-color: #0f171b;
}
.dark-mode .header {
  background-color: #534432;
}
.dark-mode p,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode label,
.dark-mode legend {
  color: #FCFCFC;
}
.dark-mode .contenido-anuncio {
  background-color: #19252b;
}
.dark-mode .contenido-anuncio img {
  filter: invert(100%);
}
.dark-mode input,
.dark-mode textarea {
  color: #FCFCFC;
  background-color: #0f171b;
}
.dark-mode .testimonio,
.dark-mode .articulo,
.dark-mode .formulario-contacto {
  background-color: #19252b;
}

.alerta.error {
  background-color: none;
  border: solid 0.2rem #d15a45;
  margin: 0.8rem 0;
  padding: 0.8rem 1.6rem;
  color: #d15a45;
  font-weight: bold;
}
.alerta.exito {
  background-color: none;
  border: solid 0.2rem #45d158;
  margin: 0.8rem 0;
  padding: 0.8rem 1.6rem;
  color: #45d158;
  font-weight: bold;
}