.seccion-formulario-contacto {

    picture {
        height: auto;
        width: 100%;

        img {
            margin-top: 4rem;
            width: 100%;
            height: auto;
        }
    }
}

.formulario-contacto {
    margin: 0 auto 8rem auto;
    position: relative;
    width: 100%;
    background-color: $blanco;
    z-index: 10;

    @include tablet {
        width: 100%;
        padding: 3.2rem;
        box-shadow: 0px 4px 5px 0px rgba($color: #000000, $alpha: .2);
    }

    @include desktop {
        margin-top: 4rem;
        width: 80%;
    }

    h2 {
        margin-bottom: 2.4rem;
    }

    fieldset {
        padding-bottom: 3.2rem;
    }

    .input-formulario {

        display: flex;
        flex-direction: column;
        margin-top: 1.6rem;

        input,
        select,
        textarea {
            resize: none;
            padding: .8rem;
        }
    }
    textarea{
        height: 16rem;
    }

    p {
        margin-top: 1.6rem;
    }

    .boton-enviar {
        cursor: pointer;
        margin-top: 2.4rem;
        border: none;
        font-family: $fuente2;
        text-transform: uppercase;
        @include boton($amarillo, inline-block);
        padding: 1.6rem 3.2rem;
    }
}